
import {API} from '../config';



// get all products
export const getTipuls = () => {
    return fetch(`${API}/tipuls`,{
        method: "GET"
        
    })
    .then(response => {
        return response.json()
    })
    .catch(err => {
        console.log(err)
    })
}
// delete single product
export const deleteTipul = (tipulId, userId, token) => {
    return fetch(`${API}/tipul/${tipulId}/${userId}`, {
        method: "DELETE",
        headers:{
            Accept:'application/json',
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`
        },

    })
    .then(response => {
        return response.json()
    })
    .catch(err => {
        console.log(err)
    })
}
//get single product
export const getTipul = (tipulId) => {
    return fetch(`${API}/tipul/${tipulId}`,{
        method: "GET"
        
    })
    .then(response => {
        return response.json()
    })
    .catch(err => {
        console.log(err)
    })
}
//update single product
export const updateTipul = (tipulId, userId, token, tipul) => {
    return fetch(`${API}/tipul/${tipulId}/${userId}`, {
        method: "PUT",
        headers:{
          
            Accept:'application/json',
            Authorization: `Bearer ${token}`
        },
        body: tipul
    })
    .then(response => {
        return response.json()
    })
    .catch(err => {
        console.log(err)
    })
}
