import React, {useState} from 'react';
import {signin, authenticate, isAuthenticated} from '../../auth/index';
import { Redirect } from 'react-router-dom';
import {
    Button,
    Card,
    CardHeader,
    Container,
    CardBody,
    FormGroup,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Row,
    Col
  } from "reactstrap";
import Unregisteredlayout from '../../layouts/Unregister';




export default function Signin() {
  const [values, setValues] = useState({
    personalnumber: '',
    password: '',
    error: '',
    loading: false,
    redirectToReferrer: false,
})
const {personalnumber, password, error, loading, redirectToReferrer} = values
const {user} = isAuthenticated()
const handleChange = name => event => {
      setValues({...values, error: false, [name]: event.target.value})
    }
const clickSubmit = (event) => {
      event.preventDefault()
      setValues({...values, error:false, loading:true})
      signin({personalnumber, password})
        .then(data => {
            if(data.error){
                setValues({...values, error:data.error, loading:false})
            } else{
                authenticate(data,() => {
                    setValues({
                        ...values,
                        redirectToReferrer: true
                    })
                })
            }
        })
  }
/*   const showError = () => (
   
) */

const redirectUser = () =>{
    if(redirectToReferrer) {
       if(user && user.role ===1) {
        return <Redirect to ="admin/dashboard" />;
       } else {
           return <Redirect to ="/" />;
       }
    }
}

const signInForm = () => (
  <>
  <Unregisteredlayout/> 
  <Container className="mt--8 pb-5">
  <Row className="justify-content-center">
       
     
  <Col lg="5" md="7">
  <Card className="bg-secondary shadow border-0">
    
    <CardBody className="px-lg-5 py-lg-5">
      <div className="text-center text-muted mb-4">
        <small>התחברות</small>
      </div>
      <Form role="form" >
        <FormGroup className="mb-3">
          <InputGroup className="input-group-alternative">
            <InputGroupAddon addonType="prepend">
              <InputGroupText>
                <i className="ni ni-email-83" />
              </InputGroupText>
            </InputGroupAddon>
            <Input onChange ={handleChange('personalnumber')} placeholder="מספר אישי"  type="string" value={personalnumber}  />
          
          </InputGroup>
        </FormGroup>
        <FormGroup>
          <InputGroup className="input-group-alternative">
            <InputGroupAddon addonType="prepend">
              <InputGroupText>
                <i className="ni ni-lock-circle-open" />
              </InputGroupText>
            </InputGroupAddon>
            <Input onChange ={handleChange('password')} placeholder="סיסמא" type="password"  value={password} />
          </InputGroup>
        </FormGroup>
      
        <div className="text-center">
        <button onClick={clickSubmit} className="btn btn-primary">התחבר</button>
        </div>
      </Form>
    </CardBody>
  </Card>
  
</Col>
</Row>
</Container>
</>
)


  return (
    <>


{signInForm()}
{redirectUser()}
</>
)
}